.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

* {
  font-weight: 500;
}

/* ANTD - LAYOUT */
.ant-layout {
  min-height: 100vh;
}

.ant-layout-footer {
  padding: 0px 16px;
  height: 48px;
  line-height: 48px;
}

.ant-layout-header {
  background: #fff;
}

.ant-layout-content {
  margin: 0px 16px;
}

.ant-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin: 4px 0px;
}

/* MENU - TITLE ITEM */
.ant-menu-inline-collapsed .ant-menu-item-group-title {
  height: 2px;
  padding: 0px;
  background: orange;
}

.ant-menu-item-group-title {
  color: orange !important;
  font-weight: 500;
  transition: none;
}

/* PADDING - MARGIN */
.ant-menu-submenu {
  padding-bottom: 0px !important;
}

.ant-menu-submenu-title {
  margin-bottom: 0px !important;
}

.ant-menu-item {
  margin: 4px 0px !important;
}

.ant-menu-item-group-title {
  margin: 4px 0px !important;
}

/* BACKGROUND COLOR */
.ant-menu li:hover {
  background: #123456 !important;
}

.ant-menu.ant-menu-dark .ant-menu-submenu-title {
  background: #0f1f2f;
}

.ant-menu.ant-menu-dark .ant-menu-item-selected {
  background: cornflowerblue;
}

.ant-menu.ant-menu-dark .ant-menu-item-selected:hover {
  background: dodgerblue !important;
}

/* WIRD FORM */
.wird-form .ant-form-item-label {
  display: block;
  text-align: left;
  line-height: unset;
  padding: 0px;
}

.ant-form-item-required:before {
  display: none;
}

.ant-form-item-required:after {
  content: '\273B' !important;
  display: inline-block !important;
  font-weight: normal;
  color: orangered;
}

.ant-form-item-no-explain .ant-form-explain {
  display: none;
}

/* BUTTON */
.ant-btn {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.mx-4 {
  margin: 0 4px;
}

.ml-4 {
  margin-left: 4px;
}

.mr-4 {
  margin-right: 4px;
}

.mb-4 {
  margin-bottom: 4px;
}

.mb-0 {
  margin-bottom: 0px;
}

.text-link {
  color: #20a8d8;
  cursor: pointer;
}

.text-link:hover {
  color: #167495;
}

.text-link-orange {
  color: hotpink;
  cursor: pointer;
}

.text-link-orange:hover {
  color: red;
}

.cursor-pointer {
  cursor: pointer;
}

.rst__rowTitle {
  font-weight: 500;
}

.rst__rowContents {
  min-width: 360px;
}

.rst__rowContents .rst__rowTitle {
  color: brown;
}
.rst__rowContentsDragDisabled .rst__rowTitle {
  color: dimgrey;
}

.rst__tree {
  min-height: 1vh !important;
  height: unset !important;
}

.rst__tree > div:first-child {
  overflow: auto hidden !important;
  height: unset !important;
  width: unset !important;
}

.rst__virtualScrollOverride {
  overflow: unset !important;
}

.ReactVirtualized__Grid.ReactVirtualized__List.rst__virtualScrollOverride {
  height: unset !important;
}

.rst__answer-text-field {
  width: 540px;
}
.rst__answer-text-field .rst__rowLabel {
  display: none;
}
.rst__answer-text-field .rst__rowToolbar,
.rst__answer-text-field .rst__toolbarButton {
  width: 100%;
}
.rst__answer-text-field .rst__toolbarButton > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.rst__answer-text-field .ant-form-item {
  width: 100%;
  margin-right: 20px;
}
.rst__answer-text-label .ant-form-item {
  display: none;
}

